@import "./variables";

*, *::before, *::after {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    outline: none;
    border-radius: 0px;
    text-decoration: none;
    font-family: '', sans-serif; }

body {
    min-height: 100vh;
    display: flex;
    flex-direction: column; }

.header, .footer {
    flex-grow: 0; }

.main {
    flex-grow: 1; }

.text_white {
    color: #ffffff; }
