@import "./variables";

.header {
    &__top {
        display: flex;
        align-items: center;
        justify-content: space-between;
        background-color: $main-blue;
        padding: 20px 60px;
        &-social {
            display: flex;
            align-items: center;
            list-style-type: none; }
        &-content {
            display: flex;
            align-items: center;
            width: 100%;
            justify-content: space-between;
            max-width: 1300px; } }
    &__social {
        &-item {
            display: inline-block;
            & + & {
                margin-left: 10px; } }
        svg {
            height: 20px;
            width: 20px; } } }
