.header__top {
  display: flex;
  align-items: center;
  justify-content: space-between;
  background-color: #0357a3;
  padding: 20px 60px;
}

.header__top-social {
  display: flex;
  align-items: center;
  list-style-type: none;
}

.header__top-content {
  display: flex;
  align-items: center;
  width: 100%;
  justify-content: space-between;
  max-width: 1300px;
}

.header__social-item {
  display: inline-block;
}

.header__social-item + .header__social-item {
  margin-left: 10px;
}

.header__social svg {
  height: 20px;
  width: 20px;
}
